@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;700&display=swap);
:root{--main-dark: #272727;--main-light: #ffffff;--helper-grey: #efefef;--helper-dark: #040301}.general{color:var(--white);font-family:'Quicksand', sans-serif;text-shadow:0 2px 0 rgba(0,0,0,0.07);letter-spacing:-1px;background-color:#040301;background-color:var(--helper-dark)}

.error__container{height:100vh;background-color:var(--helper-dark)}

.loader__container{height:100vh;background-color:var(--helper-dark)}.loader__container p{font-size:3rem;color:var(--main-light)}.loader__dots--animation span{display:inline-block;width:10px;height:10px;border-radius:100%;background-color:var(--main-dark);margin:0px 5px;background-color:var(--main-light)}.loader__dots--animation span:nth-child(1){-webkit-animation:bounce 1s ease-in-out infinite;animation:bounce 1s ease-in-out infinite}.loader__dots--animation span:nth-child(2){-webkit-animation:bounce 1s ease-in-out 0.33s infinite;animation:bounce 1s ease-in-out 0.33s infinite}.loader__dots--animation span:nth-child(3){-webkit-animation:bounce 1s ease-in-out 0.66s infinite;animation:bounce 1s ease-in-out 0.66s infinite}@-webkit-keyframes bounce{0%,75%,100%{-webkit-transform:translateY(0);transform:translateY(0)}25%{-webkit-transform:translateY(-20px);transform:translateY(-20px)}}@keyframes bounce{0%,75%,100%{-webkit-transform:translateY(0);transform:translateY(0)}25%{-webkit-transform:translateY(-20px);transform:translateY(-20px)}}

.logo{width:25%}.logo g{fill:var(--main-light)}@media (max-width: 900px){.logo{width:50%}}

.nav__button{background-color:transparent;border:none;display:flex;align-items:center;height:40px;width:40px}.nav__button__icon{width:30px;height:2px;border-radius:5px;background:var(--main-light);transition:all 0.5s ease-in-out;z-index:2}.nav__button__icon::before,.nav__button__icon::after{content:'';position:absolute;width:30px;height:2px;background:var(--main-light);border-radius:5px;transition:all 0.5s ease-in-out}.nav__button__icon::before{-webkit-transform:translate(-15px, -10px);transform:translate(-15px, -10px)}.nav__button__icon::after{-webkit-transform:translate(-15px, 10px);transform:translate(-15px, 10px)}.nav__button .open{-webkit-transform:translateX(-55px);transform:translateX(-55px)}.nav__button .open::before{-webkit-transform:rotate(45deg) translate(35px, -35px);transform:rotate(45deg) translate(35px, -35px)}.nav__button .open::after{-webkit-transform:rotate(-45deg) translate(35px, 35px);transform:rotate(-45deg) translate(35px, 35px)}

.nav__sidebar{background-color:var(--main-dark);height:100vh;left:-10px;transition:all 0.5s ease-in-out;font-weight:700;width:300px;z-index:1}.nav--animate{-webkit-transform:translateX(-1500px);transform:translateX(-1500px)}

.nav__link{margin:10px;font-weight:700}.nav__link a,.nav__link a:hover{color:var(--helper-grey);text-decoration:none}.nav__link a::after{content:'';display:block;width:0;height:1px;background:var(--helper-grey);transition:width 0.3s ease-in-out}.nav__link a:hover::after{width:100%}

.header__name{font-weight:700;font-size:50px}.header__description{font-size:40px}@media only screen and (min-device-width: 375px) and (max-device-width: 812px) and (-webkit-min-device-pixel-ratio: 3){.header__description{font-size:25px}}

.home__container{background-image:url("https://www.spacex.com/static/images/backgrounds/dragon_feature.webp");background-position:center center;background-size:cover;height:100vh}

.article{position:relative;color:var(--main-dark)}.article__image{width:100%}.article__header{width:100%;position:absolute;bottom:0px;font-weight:700}.article__header h5{width:-webkit-max-content;width:max-content;color:var(--main-dark);background-color:rgba(255,255,255,0.9)}.article__header p{font-size:1.4rem;color:var(--main-light);background-color:rgba(33,33,33,0.65)}

.rocket__header__container{width:100%;bottom:450px}.rocket__header__text{font:100px/85px D-DIN-Bold, Arial, Verdana, sans-serif;font-weight:700;text-align:center}@media (max-width: 900px){.rocket__header__container{bottom:350px}.rocket__header__text{font-size:4rem}}

.info-table h1,.info-table h5,.info-table tbody{font-weight:900}.info-table tr{color:var(--main-light)}.info-table tr:first-child{border-top:hidden}.info-table td{border-bottom:1px solid rgba(239,239,239,0.5)}.info-table td:nth-child(even){text-align:end}

.rocket{background-color:var(--main-dark)}

